<template>
  <div class="modal fade" id="memberChoiseModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">
            選擇的客戶：
            <div class="fs-12">
              提示: 需要選擇一名客戶才能開始制作表單
            </div>
          </h5>

          <listing-member-face-card :the_member="memberChoise" v-if="memberChoise!=null"></listing-member-face-card>
          <!-- div class="input-group members-search-panel">
            <input type="text" class="form-control" placeholder="請輸入名稱或編號" aria-label="請輸入名稱或編號" aria-describedby="memberSearch">
            <div class="input-group-append">
              <button class="btn btn-outline-info" type="button" id="memberSearch">篩選</button>
            </div>
          </div -->

        </div>
        <div class="modal-body pt-0 pb-0">
          
          <div class="">

            <table class="table table-top-line-none" v-if="members.length > 0">
              <tr v-for="m in members" :key="m.id" 
                :class="{ 'item-choise': m.id == memberChoise.id }" 
                @click="choised(m.id)" class="hand">

                <td>{{ m.num }}</td>
                <td>{{ m.named }}</td>
                <td>{{ m.addr }}</td>
              </tr>
            </table>

          </div>

        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">離開</button>
          <button type="button" class="btn btn-main" @click="choiseSubmit">確認選擇</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import ListingMemberFaceCard from '../../Card/ListingMemberFaceCard.vue'

export default {
  components: { ListingMemberFaceCard },
  name: 'MemberChoiseModal',
  data() {
    return {
      members: [],
      memberChoise: null
    }
  },
  mounted() {
    conn.member.getList_Member().then(res => {
      this.loadFirstChoise(res[0])
      this.members = res
    })
  },
  methods: {
    loadFirstChoise(membery) {
      if (this.memberChoise == null) { this.memberChoise = membery }
    },
    choised(id) {
      for(const m of this.members) {
        if(m.id == id) {
          this.memberChoise = m
        }
      }
    },
    choiseSubmit() {
      if (this.memberChoise == null) {
        tool.showToast('您還未選擇壹名客戶')
      } else {
        this.$emit('returnChoised', this.memberChoise)
        tool.closeModal('memberChoiseModal')
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.members-search-panel, .member-table
  margin: 0 !important

.members-panel
  border-top: none

.member-table tr:first-child > td
  border: none

</style>


