<template>
  <def-layout>
    <div slot="layout_title">修改報價單</div>

    <prices-plus-top 
      slot="layout_inner_head"
      :_freights="price_content"
      :_select="select"
    >
    <div slot="title">編輯菜品</div>
    </prices-plus-top>

    <prices-plus-body slot="layout_inner_body"
      :selected="select"
      :prices_contented="price_content"
      @removeItemChild="removeItem"
      @addItemChild="addItem"
      @changeNum_Father="changeNum"
      v-if="price_content.length > 0"
    >
    </prices-plus-body>

    <loading v-else-if="isLoading" slot="layout_inner_body"></loading>
    <empty v-else slot="layout_inner_body"></empty>

    <div slot="layout_footer">
      <prices-plus-footer
        :membery="member"
        :isPlus="false"

        @reciverTimed_Father="reciverTimed"
        @reciverNumed_Father="reciverNumed"
        @reciverPayTime_Father="reciverPayTime"
        @reciverPayway_Father="reciverPayway"

        @changeMember_Father="changeMember"
        @pricesSubmit_Father="pricesSubmit"
        @pricesPdfView_Father="pricesPdfView"

        ref="ppFooter"
      ></prices-plus-footer>
    </div>

    <div slot="layout_extra">
      <member-choise-modal @returnChoised="reciverChoised" ref="memberChoiseModal"></member-choise-modal>
      <prices-pdf-modal 
      
        :the_member="member" 
        :the_prices_timed="timed_prices" 
        :the_prices_numed="numed_prices"
        :the_prices_payway="payway_prices"
        :the_prices_paytime="paytime_prices_named"
        :the_prices_content="select_content"

        :isPlus="true"
        @submit_Father="pricesSubmit"
      >
      <div slot="submitText">確認修改</div>
      </prices-pdf-modal>

      <insert-freight-modal 
        :haved="price_content" 
        ref="insertFreight"

        v-if="isLoading"
        @reciverInsert="reciverInsertFreight"
      >
      </insert-freight-modal>

      <!-- custom-modal
        :id_prefix="'pricesUpdate'"
        :title="'溫馨提示'"
        :txt="`
          <ul>
            <li>修改後的報價單會影響之後的發票內容</li>
            <li>確定要修改嗎？</li>
          </ul>
        `"
      ></custom-modal -->
    </div>

  </def-layout>
</template>

<script>
import $ from 'jquery'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'
import config from '@/common/config'

import DefLayout from '@/components/Layout/DefLayout.vue'
import PricesPlusBody from './Body/PricesPlusBody.vue'

import conn from '@/service/conn'
import MemberChoiseModal from '@/components/Modal/Choise/MemberChoiseModal.vue'

import PricesPlusFooter from './Footer/PricesPlusFooter.vue'
import PricesPdfModal from '@/components/Modal/Pdf/PricesPdfModal.vue'
import PricesPlusTop from './Top/PricesPlusTop.vue'
import Loading from '@/components/Shimmer/Loading.vue'
import InsertFreightModal from '@/components/Modal/Insert/InsertFreightModal.vue'
import Empty from '@/components/Shimmer/Empty.vue'

export default {
  components: { 
    InsertFreightModal, DefLayout, PricesPlusBody, 
    MemberChoiseModal, PricesPlusFooter, 
    PricesPdfModal, PricesPlusTop, Loading,
    Empty 
  },
  name: 'PricesPlus',
  data() {
    return {
      price_content: [],
      select: [], // id
      select_content: [],

      member: null,
      numed_prices: '',
      timed_prices: '',
      payway_prices: 0,
      paytime_prices: 0,
      paytime_prices_named: '',

      isLoading: true,

      priceContentId: 2,
      priceId: 0
    }
  },
  mounted() {

    // 獲取報價單
    this.priceContentId = this.$route.query.priceContentId
    this.loadUpdatePC(this.priceContentId)
  },
  methods: {
    loadUpdatePC(id) {
      if ((id != null) || (id != undefined)) {
        conn.prices_content.getOne(id).then(res => {
          if (conn.judge_response(res)) {
            
            this.member = res.price_collect.membery

            this.priceId = res.price_collect.id
            
            const rc = JSON.parse(res.content)
            for (const i in rc) {
              this.select.push(rc[i].id)
            }

            this.payway_prices = res.price_collect.pay_way
            this.numed_prices = res.price_collect.num
            this.timed_prices = tool.timed.ser_timed(res.price_collect.start_time, false, true)

            this.price_content = rc
            this.$refs.ppFooter.locationData(this.numed_prices, this.timed_prices, this.payway_prices)

            if (res.price_collect) { this.$refs.ppFooter.changeSomthing(res.price_collect.pay_time.named) }

          }
        }).catch(err => {
          tool.showToast('A網絡連接失敗。')
        })
      } else {
        tool.showToast('未找到要修改的報價單對象！！！', 2800)
        
        tool.closeAllModal()
        setTimeout(() => {
          this.$router.push('/home/prices')
        }, 700)
      }
    },
    // 後臺事件
    _validate() {

      let res = validate.val_timed(this.timed_prices, '報價時間', true)
      if (res != true) { tool.showToast(res); return false }
      return true
    }, 
    _collect() {
      this.select_content = this.price_content.filter((e) => tool.inList(e.id, this.select))
    },
    _submit() {
      this._collect()

      const data = {
        num: this.numed_prices,
        start_time: this.timed_prices + ' 00:00:00',
        draft_status: 'False',
        
        freight_num: this.select_content.length,
        pay_time_id: this.paytime_prices,
        pay_way: this.payway_prices
      }
      
      conn.prices.update(data, this.priceId).then(res => {
        
        if (conn.judge_response(res)) {

          conn.prices_content.update(
            {
              content: JSON.stringify(this.select_content)
            }, 
            this.priceContentId
          ).then(res => {

            if (conn.judge_response(res)) {

              tool.showToast('報價單修改成功！！！')

              tool.closeAllModal()
              
              tool.storage._set('prices_active_member', '')
              setTimeout(() => {
                this.$router.push('/home/prices')
              }, 700)
              
            }
          })
        }
      })
      
    },
    // 界面操作事件
    pricesPdfView() {
      this._collect()
      tool.showModal('pricesPdfViewModal')
    },
    pricesSubmit() {
      let res = ''
      if (this.timed_prices == '') {
        res = '請檢查報價時間的輸入！'
        // tool.focus('input_prices_timed')
      }
      if (this.numed_prices == '') {
        res = '請檢查報價單號的輸入！'
      }
      if (res != '') { tool.showToast(res) } else {

        if (!this._validate()) { return }

        this._submit()
      }
    },
    //
    changeMember() {
      if (this.member != null) { this.$refs.memberChoiseModal.loadFirstChoise(this.member) }
      tool.showModal('memberChoiseModal')
    },
    removeItem(id) {
      const i = tool.indexList(id, this.select)
      if (this.select.length < 2) {
        tool.showToast('至少選擇壹項菜品。')
      } else {
        this.select.splice(i, 1)
      }
    },
    addItem(id) {
      const i = tool.indexList(id, this.select)
      if (i < 0) {
        this.select.push(id)
      }
    },
    changeNum(index, v) {
      this.price_content[index].price = v
    },

    openFreightInsert() {
      this.$refs.insertFreight.openInsertFreight()
      this.$refs.insertFreight.loadHaventFreight()
    },  
    // 數據接收事件
    reciverChoised(v) {
      this.member = v
      tool.storage._set('prices_active_member', JSON.stringify(v))
      // this.loadFreightsToContent()
      this.$refs.ppFooter.changeSomthing(v.pay_time.named)
    },
    reciverTimed(v) {
      this.timed_prices = v
    },
    reciverNumed(v) {
      this.numed_prices = v
    },
    reciverPayTime(v, named) {
      this.paytime_prices = v
      this.paytime_prices_named = named
    },
    reciverPayway(v) {
      this.payway_prices = v
    },
    reciverInsertFreight(v) {
      this.price_content.unshift({
        ...v
      })
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

</style>


