<template>
  <table class="table m-0 table-top-line-none">
    <tbody class="listing-tbody">
      <tr v-for="(f, index) in prices_contented" :key="f.id" class="prices-item" :class="{ inActive: !inList(f.id, selected), 'bg-white': inList(f.id, selected) }">
        
        <td class="text-accent" width="10%">{{ index + 1 }}</td>
        <td class="" width="15%">{{ f.num }}</td>
        <td class="" width="30%">{{ f.named }}</td>

        <td class="table-input-td " width="25%">
            <div class="float-left mt-1 text-input">$</div>
            <div class="input-group input-group-sm float-left listing-num-input">
              <input 
                type="text" 
                class="form-control" 
                :id="'price_' + f.id" 
                :value="f.price" 
                @keyup.enter="changeNum(f.id, index)"
                @change="changeNum(f.id, index)"
              >
              <div class="input-group-append">
                <span class="input-group-text" id="basic-addon2">/{{ unit(f.unit) }}</span>
              </div>
            </div>
        </td>

        <td width="10%"></td>
        <td width="10%" class="text-center">
          <div v-show="inList(f.id, selected)">
            <i class="fa hand listing-table-icon fa-remove" @click="itemOption(f.id, 'remove')"></i>
          </div>
          <div v-show="!inList(f.id, selected)">
            <i class="fa hand listing-table-icon fa-plus" @click="itemOption(f.id, 'add')"></i>
          </div>
        </td>

      </tr>
    </tbody>
  </table>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'PricePlusLeft',
  props: {
    prices_contented: {
      type: Array
    },
    selected: {
      type: Array
    }
  },
  mounted() {
    
  },
  methods: {
    unit(u) {
      return view.ser_unit(u)
    },
    inList(n, L) {
      return tool.inList(n, L)
    },

    //
    itemOption(id, option) {
      if (option == 'add') {
        this.$emit('addItemChild', id)
      } 
      else if (option == 'remove') {
        this.$emit('removeItemChild', id)
      }
    },

    // 调用父类的方法
    changeNum(id, index) {
      const val = $('#price_' + id).val()
      this.$emit('changeNum_Father', index, val)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/choise.sass"
@import "@/common/sass/content.sass"

</style>


