<template>
  <div class="row">
    <div class="col-sm-3">

      <h5 class="fw-none text-accent">其他信息</h5>
      <listing-member-face-card :is_icon="isPlus" :the_member="membery" v-show="membery!=null" @changeSignal="changeMember"></listing-member-face-card>
      <div v-show="membery==null" class="member-loading-wrapper">
        <loading-middle></loading-middle>
      </div>

    </div>
    <div class="col-sm-3 pt-4">
      
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverNumed" 
          :the_name="'prices_numd'"
          :the_tip_header="`報價單號`"
          :the_placeholder="`- -`"
          :the_min_length="3"
          :the_max_length="12"

          ref="numRef"
          >
          <div slot="input_txt">報價單號：</div>
        </simple-input>
      </div>
      <div class="fs-14 timed-wrapper">
        <simple-input 
          @returnValue="reciverTimed" 
          :the_name="'prices_timed'"
          :the_tip_header="`報價時間`"
          :the_placeholder="`yyyy-mm-dd`"

          ref="timedRef"
          >
          <div slot="input_txt" @click="loadingTimed">報價日期：</div>
        </simple-input>
      </div>

    </div>
    <div class="col-sm-3 pt-4">

      <div class="fs-14 timed-wrapper">
        <simple-select 
          @returnValue="reciverPayway" 
          :the_name="'prices_pay_time'" 
          :the_list="pay_ways"

          ref="payWayRef"
        >
          <div slot="input_txt">付款方式：</div>
        </simple-select>
      </div>
      <br/>
      <div class="fs-14 timed-wrapper">
        <simple-select 
          @returnValue="reciverPayTime" 
          :the_name="'prices_pay_time'" 
          :the_list="pay_times"

          ref="payTimeRef"
        >
          <div slot="input_txt">付款期限：</div>
        </simple-select>
      </div>

    </div>
    <div class="col-sm-1 flex-center">
      <button @click="_pricesSubmit" id="pricesResBtn" class="hidden"></button>
    </div>
    <div class="col-sm-2 pt-2">
      
      <button class="btn btn-light btn-block btn-sm" 
        @click="addIntoFreight">
        <span class="fs-16">+</span>
        加入產品
      </button>

      <button class="btn btn-outline-info btn-block btn-sm"
        @click="pricesPdfView">下一步</button>

    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import view from '@/common/es/view'

import SimpleInput from '@/components/Input/Simple/SimpleInput.vue'
import SimpleSelect from '@/components/Input/Simple/SimpleSelect.vue'

import ListingMemberFaceCard from '@/components/Card/ListingMemberFaceCard.vue'
import LoadingMiddle from '@/components/Shimmer/LoadingMiddle.vue'

import conn from '@/service/conn'

export default {
  name: 'PricesPlusFooter',
  components: {
    SimpleInput,
    SimpleSelect,
    ListingMemberFaceCard,
    LoadingMiddle
  },
  props: {
    membery: {
      type: Object
    },
    isPlus: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      pay_way: 0,
      timed_prices: '',
      numed_prices: '',
      pay_time_prices: '',

      pay_times: [

      ],
      origin_pay_times: [

      ],
      pay_ways: [

      ]
    }
  },
  async mounted() {
    this.pay_ways = view.show_paymet()
    
    await this.loadPayTime()
    await this.loadingTimed()
  },
  methods: {
    async loadPayTime() {
      let res = await conn.pay_time.getList({}, true)

      if (res) {
        this.origin_pay_times = res
        this.pay_times = res.map(e => e.named)
        
        if (this.membery) this.changeSomthing(this.membery.pay_time.named)
      }
    },
    changeSomthing(pay_time_named) {
      if (pay_time_named) {
        this.origin_pay_times.map((e, i) => {

          if (e.named == pay_time_named) { 
            this.reciverPayTime(i)
            this.$refs.payTimeRef.locationData(i)
          }
        })
      }
    },
    _pricesSubmit() {
      this.$emit('pricesSubmit_Father')
    },
    pricesSubmit() {
      tool.click('pricesResBtn')
    },

    pricesPdfView() {
      this.$emit('pricesPdfView_Father')
    },
    reciverTimed(v) {
      this.timed_prices = v
      this.$emit('reciverTimed_Father', this.timed_prices)
    },
    reciverPayway(v) {
      this.pay_way = v
      this.$emit('reciverPayway_Father', v)
    },
    reciverPayTime(v) {
      this.pay_time_prices = this.origin_pay_times[v].named
      this.$emit('reciverPayTime_Father', this.origin_pay_times[v].id, this.pay_time_prices)
    },
    reciverNumed(v) {
      this.numed_prices = v

      conn.prices.getOneByAny({
        num: v
      }).then(res => {
        if (conn.judge_response(res[0])) {

          this.$emit('reciverNumed_Father', '')
          tool.showToast('報價單號“' + v + '”重復，數據庫中已有相同單號的報價單！')
          
        } else {
          this.$emit('reciverNumed_Father', this.numed_prices)
        }
      })

    },
    changeMember() {
      this.$emit('changeMember_Father')
    },
    addIntoFreight() {
      tool.showModal('insertFreightModal')
    },
    locationData(num, timed, pay_way) {
      this.$refs.numRef.locationData(num)
      this.$refs.timedRef.locationData(timed)
      this.$refs.payWayRef.locationData(pay_way)
    },

    // 自动生成时间
    async loadingTimed() {
      const res = tool.timed.getNumedTimed('-')
      this.reciverTimed(res)
      this.$refs.timedRef.locationData(res)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

.timed-wrapper
  display: inline-block
  line-height: 1
  height: 24px

.member-loading-wrapper
  height: 70%
</style>


