<template>
  
  <div>

    <div class="row of-visible">
      <div class="col-sm-6 pt-2">
        <h5 class="fw-none">
          <slot name="title">
            選擇產品
          </slot>
        </h5>
      </div>
      <div class="col-sm-6 pt-1 text-right of-visible" v-if="_freights.length > 0">

        <sort-option :_list="_freights"></sort-option>

        <select-option :_listFull="_freights" :_list="_select"></select-option>
        
      </div>
      <div class="col-sm-6 d-flex justify-content-end of-visible" v-else>
        <div class="right-loading">
          <loading-small></loading-small>
        </div>
      </div>
    </div>

    <table class="table m-0">
      <tr class="text-left">
        <td width="10%">序號</td>
        <td width="15%">貨號</td>
        <td width="30%">描述</td>
        <td width="25%">
          單價
          <!-- help-icon :help_code="'prices_input'"></help-icon -->
        </td>
        <td width="10%"></td>
        <td width="10%" class="text-center">選擇</td>
      </tr>
    </table>

  </div>

</template>

<script>
import tool from '@/common/es/tool'

import HelpIcon from '@/components/Modal/Help/HelpIcon.vue'
import SortOption from '@/components/Filter/SortOption.vue'
import SelectOption from '@/components/Filter/SelectOption.vue'
import LoadingSmall from '@/components/Shimmer/LoadingSmall.vue'

export default {
  components: { HelpIcon, SortOption, SelectOption, LoadingSmall },
  name: 'PricesPlusTop',
  props: {
    _freights: {
      type: Array,
    },
    _select: {
      type: Array
    }
  },
  data() {
    return {
      isSort: false,
      isAll: false
    }
  },
  methods: {
    sortOption() {
      this._freights.reverse()
      this.isSort = !this.isSort
      if (this.isSort) { tool.showToast('逆序排列') } else { tool.showToast('順序排列') }
    },
    selectOption() {
      if (!this.isAll) {
        const none = this._freights.filter((e) => {
          return !tool.inList(e.id, this._select)
        })
        
        for (const i of none) {
          this._select.push(i.id)
        }
        this.isAll = true

        tool.showToast('全選')
      } else {
        this._select.splice(1, (this._select.length - 1))
        this.isAll = false

        tool.showToast('移除全選')
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


